<template>
<div class="exam-admin">
  <p>  <el-button type="text" @click="dialogFormVisible = true"></el-button></p>
  <el-dialog title="" :visible.sync="dialogFormVisible">
    <el-form :model="form" :rules="rules" ref="form">
      <el-form-item label="" :label-width="formLabelWidth" prop="name">
        <el-input v-model="form.name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="datetime1">
        <div class="block">
          <el-date-picker
            v-model="form.datetime1"
            type="datetimerange"
            placeholder="">
          </el-date-picker>
        </div>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="totalCount">
        <el-select v-model="form.totalCount" placeholder="">
          <el-option label="50" value="50"></el-option>
          <el-option label="100" value="100"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="single">
        <el-input v-model.number="form.single" type="single" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="multiple">
        <el-input v-model.number="form.multiple" type="multiple" singleauto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="jungle">
        <el-input v-model.number="form.jungle" type="jungle" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible = false"> </el-button>
      <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
    </div>
  </el-dialog>
  <el-table :data="tableData" style="width: 100%">
    <el-table-column prop="index" label="" width="180"></el-table-column>
    <el-table-column prop="name" label="" width="180"></el-table-column>
    <el-table-column prop="time" label=""></el-table-column>
    <el-table-column prop="number" label=""></el-table-column>
    <el-table-column label="">
      <template scope="scope">
        <el-button @click="handleClick" type="text" size="small"></el-button>
      </template>
    </el-table-column>
    <el-table-column prop="number" label="">
      <template scope="scope">
        <el-button @click="handleClick" type="text" size="small"></el-button>
      </template>
    </el-table-column>
  </el-table>
</div>

</template>
 <script>
    export default {
      name: 'adminExam',
      data () {
        var checkAge = (rule, value, callback) => {
          if (!value) {
            return callback(new Error(''))
          }
          setTimeout(() => {
            if (!Number.isInteger(value)) {
              callback(new Error(''))
            } else {
              if (+this.form.single + +this.form.multiple + +this.form.jungle > this.form.totalCount) {
                console.log(this.form)
                callback(new Error(''))
              } else {
                callback()
              }
            }
          }, 1000)
        }
        return {
          tableData: [{
            index: '1',
            name: '',
            time: '2017-05-09 14:00 - 24 08:00',
            number: 50
          }, {
            index: '2',
            name: '',
            time: '2017-05-09 14:00 - 24 08:00',
            number: 50
          }, {
            index: '3',
            name: '',
            time: '2017-05-09 14:00 - 24 08:00',
            number: 50
          }, {
            index: '4',
            name: '',
            time: '2017-05-09 14:00 - 24 08:00',
            number: 50
          }],
          dialogFormVisible: false,
          form: {
            name: '',
            datetime1: '',
            totalCount: '',
            single: '',
            multiple: '',
            jungle: '',
            delivery: false
          },
          rules: {
            name: [
              { required: true, message: '', trigger: 'blur' }
            ],
            datetime1: [
              { required: true, type: 'datetime', message: '', trigger: 'change' }
            ],
            totalCount: [
              { required: true, message: '', trigger: 'change' }
            ],
            single: [
              // { required: true, message: '', trigger: 'blur' },
              { validator: checkAge, trigger: 'blur' }
            ],
            multiple: [
              // { required: true, message: '', trigger: 'blur' },
              { validator: checkAge, trigger: 'blur' }
            ],
            jungle: [
              // { required: true, message: '', trigger: 'blur' },
              { validator: checkAge, trigger: 'blur' }
            ]
          },
          formLabelWidth: '120px'
        }
      }
    }
  </script>
  <style scoped>
  .exam-admin {
    padding: 0 15px;
  }
  p {
    line-height: 40px;
  }
  </style>
  


